// Обнуление стилей списка
@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

// Основной шрифт
@mixin font {
  font-family: "PT Sans", "Arial", sans-serif;
}

// Дуфолтный размер изображений
@mixin img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}
